body {
  min-width: 320px;
  //min-height: 100vh;
  //display: flex;
  //flex-direction: column;
  //justify-content: space-between;

  font-family: $circe;
  font-size: 20px;
  line-height: 24px;
  font-style: normal;
  font-weight: normal;

  color: $black;
  background-color: $light-pink;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  --webkit-clip-path: inset(100%);

  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}
